import bar from '../../components/bar/bar.vue';
import refresh from '../../components/refresh/refresh.vue';
export default {
    components:{
		bar,
		refresh
	},
	data(){
		return{
			articleId: null,	// 文章Id
			pages: 1,			// 数据页数
			// 自定义导航栏对象
			setNav:{
				'bg':'#FFFFFF',		// 背景色
				'navTitle': '评论列表',	// 导航标题
				'leftIcon': 'url(../../static/images/nav_left.png)',	// 左图标
				// 'rightIcon': 'url(../../static/images/nav_right.png)',	// 右图标
				// 'rightText': '发布',										// 右文本
				'isdisPlayNavTitle': true,	// 是否显示返回按钮
				'isRightType': 0,			// 右侧标签显示类型 0不显示 1图标 2文本
				// 由于导航栏是共用的，把所有的东西封装好，然后有些页面不需要的东西通过条件控制进行显示与隐藏
			},
			bindStyle: false,
			submitBtn: false,
		}
	},
	onLoad: function (options) {
		// options可以接收父页面 传过来的值
		this.articleId = options.aId;
		this.getCommentData()
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function (e) {
		console.log(e)
	},
	methods: {
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
		},
		refreshMove(e){
			this.$refs.refresh.refreshMove(e);
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
		isRefresh(){
			setTimeout(() => {
				uni.showToast({
					icon: 'success',
					mask: true, // 显示透明蒙层，防止触摸穿透
					title: '刷新成功'
				})
				this.$refs.refresh.endAfter() //刷新结束调用
			}, 1000)
		},
		// 获取所有评论数据
		getCommentData: function() {
			let that = this;
			// 显示 loading 提示框
			uni.showLoading({
				title: 'loading...'
			})
			this.$axios.request({
				url: '/articleComment/selectPageAppArticleByArticleId',
				method: 'post',
				data: {
					"articleId": that.articleId,
					"pageCount": 1,
					"pageSize": 10
				}
			}).then(function (res) {
				console.log(res.data.data.dataList);
				uni.hideLoading()
			})
		},
		// 输入框聚焦
		focusTextArea: function (e) {
			this.bindStyle = true;
		},
		// 输入框失去焦点
		bindTextArea: function (e) {
			// console.log(e.detail.value,"长度："+e.detail.value.length)
			var val = e.detail.value.replace(/(^\s*)|(\s*$)/g, "");
			// console.log(val)
			// 注释：indexOf() 方法对大小写敏感！
			// 注释：如果要检索的字符串值没有出现，则该方法返回 -1
			if (val != '' && val != null) {
				this.submitBtn = true;
			} else {
				this.bindStyle = false;
				this.submitBtn = false;
			}
		},
	}
}
